@import url(https://fonts.googleapis.com/css2?family=Hind+Vadodara&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body{
  background-color: linear-gradient(from left orange to red);
}
.App{text-align:center;background:linear-gradient(217deg, rgba(255, 0, 0, 0.8), rgba(255, 0, 0, 0) 70.71%),linear-gradient(127deg, rgba(0, 255, 0, 0.8), rgba(0, 255, 0, 0) 70.71%),linear-gradient(336deg, rgba(0, 0, 255, 0.8), rgba(0, 0, 255, 0) 70.71%)}.App-header{min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#fff}.list{z-index:1}.happyday{font-family:"Nunito",sans-serif;font-size:2.5rem;z-index:1;margin-top:35px;list-style-type:none}.mother{font-family:"Allura",cursive;font-size:80px;z-index:1;list-style-type:none}.circle{position:absolute;background-color:#cbce11;width:600px;height:600px;border-radius:50%;z-index:0}.heart-shape{position:absolute;width:300px;height:300px;background-color:#fa4242;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.heart-shape:before{position:absolute;bottom:0px;left:-150px;width:300px;height:300px;content:"";border-radius:50%;background-color:#fa4242}.heart-shape:after{position:absolute;top:-150px;right:0px;width:300px;height:300px;content:"";border-radius:50%;background-color:#fa4242}.btn{padding:1em 2.1em 1.1em;border-radius:3px;margin:10px 10px 10px 10px;margin-top:7rem;color:#fbdedb;background-color:#fbdedb;display:inline-block;background:#e94530;transition:.3s;font-family:sans-serif;font-weight:800;font-size:.85em;text-transform:uppercase;text-align:center;text-decoration:none;box-shadow:0em -0.3rem 0em rgba(0,0,0,.1) inset;position:relative}.btn:hover,.btn:focus{opacity:.8}.btn:active{-webkit-transform:scale(0.8);transform:scale(0.8)}a{text-decoration:none}
